/************** RING'S CONTAINER **************/
.moon-orbit {
  position: fixed;
  top: 50%;
  right: 0px;
  transform: translateY(-50%);
  width: 700px;
  height: 700px;
  transition: transform 1s ease-out;
  will-change: transform;
  pointer-events: none;
}

/************** MOON IN THE MIDDLE **************/
.moon {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 30em;
  height: 30em;
  background: linear-gradient(90deg, #1f1f1f 0%, #332455 40%, #40255a 100%);
  border-radius: 50%;
  z-index: 7;
  box-shadow: 0 0 50px rgba(255, 255, 255, 0.15);
  overflow: hidden;
  transform: translateX(40em) translateY(-50%);
  opacity: 0;
  transition: transform 1.8s cubic-bezier(0.22, 0.9, 0.28, 1),
    opacity 1s ease-in-out;
  will-change: transform, opacity;
}

.moon-visible {
  transform: translateX(-50%) translateY(-50%);
  opacity: 1;
}

.moon-hidden {
  transform: translateX(60em) translateY(-50%);
  opacity: 0;
  transition: transform 1.2s ease-in, opacity 1.2s ease-in;
}

.portfolio-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  pointer-events: none;
}

/************** RINGS **************/
.ring {
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  border: none;
  background: transparent;
  transform: translate(-50%, -50%);
  z-index: 1;
}

/************** COLORS AND PROGRESSIVE SIZES **************/
.ring-1 {
  width: 40em;
  height: 40em;
  background-color: rgba(142, 150, 160, 0.1);
  z-index: 6;
}

.ring-2 {
  width: 50em;
  height: 50em;
  background-color: rgba(112, 120, 132, 0.13);
  z-index: 5;
}

.ring-3 {
  width: 60em;
  height: 60em;
  background-color: rgba(88, 100, 118, 0.16);
  z-index: 4;
}

.ring-4 {
  width: 70em;
  height: 70em;
  background-color: rgba(66, 83, 104, 0.19);
  z-index: 3;
}

.ring-5 {
  width: 80em;
  height: 80em;
  background-color: rgba(48, 67, 90, 0.22);
  z-index: 2;
}

.ring-6 {
  width: 90em;
  height: 90em;
  background-color: rgba(34, 54, 77, 0.25);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  overflow: hidden;
}

.star {
  position: absolute;
  background: #fff;
  border-radius: 50%;
  opacity: 0.7;
  animation: twinkle 3s infinite ease-in-out;
  filter: drop-shadow(0 0 2px #fff);
}

/************** TWINKLING OF THE STARS **************/
@keyframes twinkle {
  0%,
  100% {
    opacity: 0.5;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.4);
  }
}

/************** RESPONSIVE **************/
@media (max-width: 767px) {
  .moon-orbit {
    width: 300px;
    height: 300px;
  }
  .moon {
    width: 19em;
    height: 19em;
  }
  .ring-1 {
    width: 23em;
    height: 23em;
  }
  .ring-2 {
    width: 27em;
    height: 27em;
  }
  .ring-3 {
    width: 31em;
    height: 31em;
  }
  .ring-4 {
    width: 35em;
    height: 35em;
  }
  .ring-5 {
    width: 39em;
    height: 39em;
  }
  .ring-6 {
    width: 43em;
    height: 43em;
  }
}
