/************** HEADER **************/
header {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 3.75rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 1rem;
  color: #fff;
  background-color: transparent;
  z-index: 1000;
}

header h1 {
  position: relative;
  font-size: 1.2rem;
  font-weight: 600;
  letter-spacing: 0.2em;
  height: 100%;
  width: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
}

/************** LIST OF ICONS **************/
#ul-header {
  list-style: none;
  display: flex;
  margin-left: auto;
  gap: 1rem;
  align-items: center;
  padding: 0;
  z-index: 10000;
}

#ul-header li a {
  font-size: clamp(1.2rem, 2vw, 1.3em);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  transition: color 0.2s ease, transform 0.2s ease;
  text-decoration: none;
}

#ul-header li a:hover {
  color: #ff5f5f;
}

#ul-header li .nav-icon {
  color: currentColor;
  transition: color 0.2s ease;
}

/************** MENU BUTTON **************/
#menu-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 0 1.5rem 0 2.5rem;
}

#menu-btn-toggle {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: clamp(1.4rem, 2.5vw, 2rem);
  color: #ff5f5f;
  transition: color 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

#menu-btn-toggle .nav-icon {
  color: currentColor;
  transition: color 0.2s ease;
}

/************** MENU FULLSCREEN **************/
.fullscreen-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #1f1f1f 0%, #332455 40%, #40255a 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transform: translateY(-100%);
  z-index: 2000;
  transition: transform 0.5s ease;
}

.fullscreen-menu.active {
  transform: translateY(0);
}

.fullscreen-menu ul {
  list-style: none;
  padding: 0;
  text-align: center;
}

.fullscreen-menu ul li {
  margin: 1em 0;
}

.fullscreen-menu ul li a {
  color: #f5f5f5;
  text-decoration: none;
  font-size: clamp(3rem, 6vw, 4.5rem);
  font-weight: 600;
  transition: color 0.3s ease, transform 0.2s ease;
}

.fullscreen-menu ul li a:hover {
  color: #ff5f5f;
  transform: scale(1.2);
  text-decoration: underline;
}

/************** CLOSE MENU BUTTON **************/
.close-btn {
  position: absolute;
  top: 0.6rem;
  right: 2.2rem;
  background: transparent;
  border: none;
  font-size: clamp(1.8rem, 3vw, 2.6rem);
  color: #ff5f5f;
  cursor: pointer;
  transition: transform 0.2s ease, color 0.2s ease;
}

/************** RESPONSIVE **************/

@media (max-width: 768px) {
  header {
    padding-right: 0;
  }
  header h1 {
    width: 100px;
  }
  #menu-btn {
    padding: 0 1.5rem 0 1.5rem;
  }
  .close-btn {
    top: 0.9rem;
    right: 1.3rem;
  }
}
